import React, { useState } from "react";
import { Typography, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    // background: "#e9ecef",
    padding: "10px",
    margin: "0px 10px",
  },
  Typography: {
    fontFamily: "Trocchi",
    alignSelf: "left",
    fontWeight: "400",
  },
}));

const Footer = (props) => {
  const classes = useStyles();

  return (
    <Box className={classes.root} id="skills">
      <Typography
        variant="h4"
        style={{
          fontFamily: "Trocchi",
        }}
      >
        Skills
      </Typography>
      <hr
        style={{
          width: "50%",
          marginTop: "0px",
          paddingTop: "0px",
          opacity: "0.6",
        }}
      ></hr>
      {Object.keys(props.skills).map((key) => {
        return (
          <div>
            <h4>{key}</h4>
            {props.skills[key].map((skill) => {
              return skill.src && skill.src !== "" ? (
                <img
                  style={{ paddingInline: "5px", height: "30px" }}
                  alt={skill.skill}
                  src={skill.src ? skill.src : null}
                />
              ) : (
                <li
                  style={{
                    color: "white",
                    borderRadius: "0.4em",
                    backgroundColor: "black",
                    fontSize: "1em",
                  }}
                >
                  {skill.skill}
                </li>
              );
            })}
          </div>
        );
      })}

      {/* <ul>
        {props.skills.map((skill) => {
          return <li style={{ fontSize: "1.1rem" }}>{skill}</li>;
        })}
      </ul> */}
    </Box>
  );
};

export default Footer;
